.cover_blanket {
  position: relative;
  --header_height: 60px;

  font-family: "Montserrat";
  font-style: normal;
  padding: 80px 15px;
  transition: all ease-in-out 0.5s;
}
.frame {
  margin: 0 auto;
  max-width: 1170px;
  padding: 0 15px;
}
.cover_blanket__shadow {
  position: relative;
  --header_height: 60px;

  font-family: "Montserrat";
  font-style: normal;
  padding: 80px 15px;
  transition: all ease-in-out 0.5s;
}

.cover_blanket__shadow::before {
  transition: all ease-in-out 0.5s;
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100vh;
  background: rgba(128, 128, 128, 0.659);
  z-index: 80;
}
