.friendMessage {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  gap: 5px;
  align-items: flex-start;
}

.friendImageAndText {
  display: flex;
  align-items: flex-end;
  gap: 20px;
}

.friendImageAndText img {
  height: 48px;
  width: 48px;
  border-radius: 50%;
}

.friendText {
  position: relative;
  background: #f5f7fb;
  border-radius: 10px;
  padding: 7px 13px;
}

.friendText:after {
  content: " ";
  position: absolute;
  width: 0;
  height: 0;
  left: auto;
  left: -15px;

  bottom: 0;
  border: 32px solid;
  border-color: transparent transparent #f5f7fb transparent;
  z-index: -1;
}

.friendName {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */

  color: #000000;
}

.friendMessageText {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  /* identical to box height */

  color: #000000;
}

.friendTime {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  font-size: 10px;
  line-height: 12px;
  margin-left: 10px;

  color: #000000;
}
