.item {
}

.item:not(:last-child) {
  /* Add your styles here */
  border-bottom: 1px solid #e5e5e5;
}

.users {
  width: 100%;
  border-collapse: collapse;
}

.buttonsContainer {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 18px;
  align-self: flex-end;
}

.container {
  width: 55%;
  display: flex;
  flex-direction: column;
}

.thead {
  font-weight: 600;
  font-size: 14px;
  line-height: 16px;
  background: #e5e5e5;
}

.nameCol {
  width: 50%;
  padding: 16px 0px 16px 19px;
}

.ageCol {
  width: 50%;
  padding: 11px 0px 9px 17px;
  font-weight: bold;
  font-size: 14px;
  line-height: 16px;
}
