.sidebar {
  position: absolute;
  width: 370px;
  height: 100vh;
  background: #fff;
  z-index: 100;
  top: 0;
  left: -100%;
  transition: 0.7s ease-in-out all;
  box-shadow:
    0px 10px 40px rgba(29, 33, 38, 0.13),
    0px 1px 2px rgba(29, 33, 38, 0.1);
}

.sidebar.open {
  left: 0;
}

.background {
}

.nav {
  margin-top: 63px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 45px;
}

.nav a {
  text-decoration: none;
  font-weight: 600;
  font-size: 22px;
  line-height: 27px;
  color: #000;
  transition: all ease-in-out 0.3s;
}

.nav a:hover {
  color: #58acff;
}

.nav a.active {
  text-decoration-line: underline;
  color: #0059b2;
}
.close {
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  padding: 5px;
  right: 24px;
  top: 16px;
  cursor: pointer;
  outline: none;
  border: none;
  background-color: #fff;
  cursor: pointer;
}
.close img {
  transition: 0.3s ease-in-out all;
}

.close:hover img {
  scale: 1.2;
}
