[data-theme="1"] {
  --color: #000000;
  --background: #ffffff;
}

[data-theme="2"] {
  --color: #000000;
  --background: #ddeeff;
  .select {
    border: 1px solid #e9e7e7;
    border-radius: 2px;
    border: 1px solid #323232;
    box-shadow: 0px 4px 4px 0px rgba(0, 0, 0, 0.25);
  }
}

[data-theme="3"] {
  --color: #ffffff;
  --background: #212a34;
  .select {
    border: 1px solid #e9e7e7;
  }
}

html {
  background-color: var(--background);
  color: var(--color);
}

.select {
  background-color: inherit;
  padding: 11px 10px;

  color: inherit;

  font-size: 14px;
  font-family: Montserrat;
  border-radius: 2px;
  border-radius: 2px;
  border: 1px solid #323232;
  outline: none;
  text-transform: capitalize;
}
